import { Dispatch, SetStateAction, useCallback, useMemo, useState } from "react"
import { ChatFeedbackData } from "../useChat/useChat.types"
import { generateChatFeedbackTags, getChatFeedbackSubmitTags } from "./useChatFeedbackData.helpers"
import { ChatFeedbackSubtagData } from "./useChatFeedbackData.types"

type UseChatFeedbackDataProps = {
  chatFeedbackData: ChatFeedbackData | undefined
}

type UseChatFeedbackDataState = {
  feedbackTags: Array<string>
  setFeedbackTags: Dispatch<SetStateAction<Array<string>>>
  feedbackComment: string
  setFeedbackComment: Dispatch<SetStateAction<string>>
  subtags: Array<ChatFeedbackSubtagData>
  tags: Array<string>
  getScrubbedSubmitTags: () => Array<string>
}

const useChatFeedbackData = ({
  chatFeedbackData,
}: UseChatFeedbackDataProps): UseChatFeedbackDataState => {
  const [feedbackTags, setFeedbackTags] = useState<Array<string>>([])
  const [feedbackComment, setFeedbackComment] = useState<string>("")

  const { subtags, tags } = useMemo(
    () => generateChatFeedbackTags(chatFeedbackData, feedbackTags),
    [chatFeedbackData, feedbackTags],
  )

  const getScrubbedSubmitTags = useCallback(
    () => getChatFeedbackSubmitTags(feedbackTags),
    [feedbackTags],
  )

  return {
    feedbackTags,
    setFeedbackTags,
    feedbackComment,
    setFeedbackComment,
    subtags,
    tags,
    getScrubbedSubmitTags,
  }
}

export { useChatFeedbackData }
export type { UseChatFeedbackDataProps, UseChatFeedbackDataState }
